import { Component } from "react";
import { Bar, Header, Button } from "../components/TextBars";
import "../Styles/global.css"
import "../Styles/login.css"
import axios from "axios";
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer, toast } from 'react-toastify';
import logo from "../components/logo2.png"
import {Link} from 'react-router-dom';


export default class Login extends Component{
    constructor(props) {
        super(props);
        this.state = {
            datavalue: '',
            id: ""
        }
    }

    

    render() {

            const logg = () => {
                if (localStorage.getItem('token') !== null) {window.location.assign('/choice')
                 console.log('123')}}

            const formSubmited = (e) => {
                e.preventDefault()
                const formData = new FormData(e.currentTarget);
                const data = Object.fromEntries(formData);
                
                this.setState({
                    datavalue: data
                })
                const req = {
                    "email": data.name,
                    "password": data.password
                }
                axios.post('http://79.174.92.124:9999/login', req)
                .then( (e) => {
                    console.log(e.data)
                    localStorage.setItem('token', e.data.user_id)
                    toast.success(' ')
                    window.location.assign('/choice')
                })
                .catch(error => {
                    let errMsg = error.response ? `   ` //    
                                               : error.request ? '   ' //     
                                                               : `  : ${error.message}`; //   
                    toast.error(errMsg); //  
                  });
            };
    

        return(<>
                <div onMouseEnter={logg} style={{textAlign: 'center', marginTop: '20px'}}>
                    <p><img src={logo} className="imegst"/></p>
                    <Link to='/' className="panel"></Link><Link to="/registration" className="panel"></Link>
                </div>
                <div className="login">
                    <Header title=""/>
                    <form onSubmit={formSubmited} >
                        <Bar type="text" for = 'name' id='name' title='' name='name'/>
                        <Bar type="password" for = 'password' id='password-input' title='' name='password'/>
                        <Button style={{marginTop: "2px"}} link = '/main' title = ''/>
                    </form>
                </div>
                <h>MOD</h>
            </>
        )
    }

    
}