import { Component } from "react";
import { TextBars, Bar, Header, Button } from "../components/TextBars";
import "../Styles/global.css"
import "../Styles/main.css"
import { toast } from "react-toastify";
import {Link} from 'react-router-dom';


export default class Login extends Component{
    render() {

        const logg = () => {
            if (localStorage.getItem('token') == null) {
                toast.error('  !')
                window.location.assign('/')
            }
        }

        const exit= () => {
            localStorage.removeItem('token')
            window.location.assign('/')
        }

        return(<>
        <div onMouseEnter={logg}>
            <div style={{textAlign: 'center', marginTop: '20px'}}>
                <Link to='/choice' className="panel"></Link>
                <Link to="/newMemb" className="panel"> </Link>
                <Link to="/ListOfMembers" className="panel"> </Link>
                <Link className="panel" style={{color: 'red'}} onClick={exit}></Link>
            </div>
            <div className='main' style={{marginTop: "150px"}}>
                <Header title=" "/>
                <Button link="/newMemb" title = ' '/>
                <Button link="/ListOfMembers" title = ''/>
            </div>
        </div>
        </>
        )
    }

    
}